
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'


export default {
directives: {
TransferDom
},
components: {
XHeader,
Flexbox,
FlexboxItem,
Group,
Cell,
Search,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Popup,
XButton,
XInput,
XTextarea
},
mounted: function () {
this.getProjectList();
},
computed: {
isShowPopup: function () {
var isShow = false;

if (this.$ISJS.existy(this.editItemObj)) {
if (this.$ISJS.existy(this.editItemObj.id)) {
if (this.editItemObj.id == -1
|| this.editItemObj.id > 0) {
isShow = true;
}
}
}

return isShow;
}
},
methods: {
getProjectList: function () {
this.BaseHttp("./project/all", {
searchText: this.searchText
}, (response) => {
this.projectList = response;
}, (errorMsg) => {
this.showAlert(errorMsg);
});
},
editProject: function (projectObj) {

this.editItemObj = projectObj;
},
showAddproject: function () {

this.editItemObj = {
id: -1
};
},
onDeleteprojectClicked: function (projectObj) {
this.showConfirm("",
()=> {
this.BaseHttp("./project/delete", {
id: projectObj.id
}, (response) => {

this.getProjectList();
}, (errorMsg) => {
this.showAlert('');
});
})
}, onCloseItemPopup: function () {
this.editItemObj = {}
}, onSavePopup: function () {

if (this.$ISJS.not.existy(this.editItemObj.name) || this.$ISJS.empty(this.editItemObj.name)) {
this.showAlert("");
return
}
if (this.$ISJS.not.existy(this.editItemObj.path) || this.$ISJS.empty(this.editItemObj.path)) {
this.showAlert("SVN");
return
}


var urlxxx = ''
if (this.editItemObj.id == -1) {

urlxxx = './project/add'
} else if (this.editItemObj.id > 0) {

urlxxx = './project/edit'
}


this.showConfirm("",()=>{
this.BaseHttp(urlxxx, this.editItemObj,
(response) => {

this.onCloseItemPopup();

this.getProjectList();
this.showAlert("");
}, (errorMsg) => {
this.showAlert(errorMsg);
});
});



}
},
data() {
return {
searchText: "",
projectList: [],
editItemObj: {}
}
}
}



